$color-primary: #3d200d;
$color-secondary: #8e4d22;
$color-tertiary: #e9e7df;

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  box-sizing: border-box;
}

.u {
  &__space-around {
    &--col {
      justify-content: space-between;
      display: flex;
      flex-direction: column;
      align-content: flex-end;
    }
  }

  &__container {
    font-size: 1.8em;
    min-height: 3.22em;
    font-family: "DM Sans", sans-serif;
    margin: 0.5em auto 1em auto;
    border-radius: 5px;
    background-color: #fff;
    &--white {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &--tall {
      margin: 0.5em auto 0 auto;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      & > li {
        width: 100%;
        text-align: left;
        padding: 0 1em;
        display: flex;
        justify-content: space-between;
      }
    }
  }
}

.TnC {
  max-width: 144em;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 44.7em auto 33.8em;

  &__aside {
    position: relative;

    &--left {
      padding: 5em 6em;
      background: #e9e7df;
      display: grid;
      grid-template-rows: 34em auto;
      & :nth-child(2) {
        justify-items: baseline;
      }
    }
    &--right {
      padding: 50px 0;
      // background: green;
      display: flex;
      flex-flow: column;
      align-items: stretch;
    }
  }

  &__HeadingContainer {
    height: 34em;
    margin-left: -6em;
  }

  &__LogoGraphics {
    &--left {
      height: 5.7em;
      width: auto;
    }
  }

  &__paragraph {
    &--left1 {
      color: $color-primary;
      font-family: "DM Serif Display", serif;
      font-size: 2.4em;
      line-height: 1.167em;
    }
    &--left2 {
      color: $color-secondary;
      font-family: "DM Sans", sans-serif;
      font-size: 1.8em;
      line-height: 1.55em;
    }
  }

  &__heading {
    position: absolute;
    left: -1000000em;
  }
}

.TnC-map {
  position: relative;
  padding: 5em 3em;
  &__wrapper {
    border: 2px solid $color-secondary;
    border-radius: 10px;
    height: 100%;
    padding: 2em;
  }
  &__heading {
    font-family: "DM Serif Display", serif;
    font-size: 2.1em;
    line-height: 1.381em;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    color: $color-primary;
  }
  &__subheading {
    color: $color-secondary;
    font-family: DM Sans;
    font-weight: normal;
    font-style: normal;
    font-size: 1em;
    line-height: 1.8em;
    text-align: center;
    text-transform: uppercase;
    padding-top: 1em;
  }
  &__map {
  }

  &__svg {
    margin: 3em 0;
    width: 100%;
    height: 60.3em;
  }
  &__brands {
    margin: 2em 0;
  }
  &__brand-logo {
    opacity: 0.2;
    cursor: pointer;
    height: 5.2em;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: 0.4s;
    &--static {
      transition: none;
      margin: 1em auto;
    }
  }

  &__brand-logo:hover {
    transform: translateY(-2px);
    opacity: 0.8;
  }

  &__brand-logo:not(:nth-child(1)) {
    margin-left: 1em;
    mix-blend-mode: normal;
  }
}

.TnC-aside {
  border-radius: 1em;
  width: 33.8em;
  // margin-bottom: 2em;

  &__section-right {
    text-align: center;
    background-color: $color-tertiary;
    padding: 3.6rem;
    text-align: center;

    &--2 {
      margin-top: 10px;
      flex: 1;
    }
    &--1 {
      height: 12em;

      & select {
        width: 100%;
        height: 2.3em;
        background: #ffffff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        font-size: 1.8em;
        padding: 0 1em;
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        background-position: right 10px top 50%;
        background-repeat: no-repeat;
      }
      & option {
        font-size: 1em;
      }
      & :focus,
      & :hover {
        // outline: none;
      }
    }
  }

  &__selected-place--name {
    padding: 1em;
    border-radius: 0.5em;
    font-weight: normal;
    font-size: 1.8em;
    color: #ffffff;
    font-family: "DM Sans", sans-serif;
    width: 100%;
    text-align: center;
    background-color: $color-primary;
  }

  &__selected-place--attr {
    height: 2.8em;
    font-family: "DM Sans", sans-serif;
    font-size: 1.4em;
    color: $color-primary;
    font-weight: bold;
  }
  & &__icon {
    padding: 3.4em 0;
  }
}

.select-box {
  display: flex;
  width: 400;
}

.select-box .options-container {
  background: #2f2f2f;
  color: #f5f6fa;
  max-height: 0;
  width: 100%;
  opacity: 0;
  transition: 0.4s;
}

.active-brand {
  opacity: 1;
}

#TnC-chart-legend {
  stroke: 1px solid $color-primary;
}
